	module.exports = {"data":{"markdownRemark":{"html":"<p>Another use of bracket notation on objects is to use a variable to access a property. This can be very useful for iterating through lists of the object properties or for doing the lookup.</p>\n<p>Here is an example of using a variable to access a property:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>var someProp = \"propName\";\nvar myObj = {\n  propName: \"Some Value\"\n}\n\nmyObj[someProp]; // \"Some Value\"</code></pre>\n      </div>\n<p>Note that we do not use quotes around the variable name when using it to access the property because we are using the value of the variable, not the name.</p>\n<p>Note: when you are coding, make sure you have a positive attitude, being negative will block you from getting solutions.</p>\n<p>This will help you! be sure to always take a break!</p>","fields":{"slug":"/certificates/accessing-object-properties-with-variables/"},"frontmatter":{"title":"Accessing Object Properties with Variables"}}},"pathContext":{"slug":"/certificates/accessing-object-properties-with-variables/"}}

